import LoginPage from '@/components/pages/LoginPage'
import BetweenPage from '@/components/pages/BetweenPage'
import Repositories from '@/components/pages/Repositories'

export default [
	{
		path: '/',
		name: 'LoginPage',
		component: LoginPage
	},
	{
		path: '/redirect',
		name: 'BetweenPage',
		component: BetweenPage
	},
	{
		path: '/my_repo',
		name: 'Repositories',
		component: Repositories
	}
]



