<template></template>
<script>
	export default {
		name: 'BetweenPage',
		mounted() {
			this.$store.dispatch('getAccessToken').then((response) => {
				this.$storage.set('token', response.access_token)
				this.$storage.set('type', response.token_type)

				this.$store.commit('logged')

				this.$router.replace({
					name: 'Repositories'
				})
			})
		}
	}
</script>


