import Vue from 'vue'
import Vuex from 'vuex'

import Authorization from './auth'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		logged: false,
		repositories: []
	},
	mutations: {
		logged(state) {
			state.logged = true
		},
		setRepo(state, data) {
			state.repositories = data
		}
	},
	modules: {
		Authorization
	}
})


