<template>
	<div class="repos">
		<div>List of repositories</div>
		<ul>
			<li v-for="repository in repositories">{{repository.name}}</li>
		</ul>
	</div>
</template>

<script>
	export default {
		name: "Repositories",
		mounted() {
			this.getRepositories()
		},
		data() {
			return {
				repositories: []
			}
		},
		methods: {
			getRepositories() {
				this.$api.get('user/repos').then((response) => {
					this.repositories = response
				})
			}
		}
	}
</script>


