import { getAccessToken as getAccessToken, getCode as getCode } from './actions'

const state = {
	User: {
		isLogged: false
	}
}

export default {
	state,
	mutations: {
		loginUser(state) {
			state.User.isLogged = true
		}
	},
	actions: {
		getAccessToken,
		getCode
	}
}


